/* overrides */
body,
div,
span,
td,
p {
  font-family: Dosis, sans-serif;
  font-size: 1.25rem;
}

h1,
h2,
h3 {
  font-family: "PT Serif", serif;
  font-weight: 300;
  text-transform: lowercase;
}

a {
  /*:active {
  	font-weight: 300;
  	text-decoration: none !important;
  	border-bottom: solid 1px;
  }*/
}
a :hover {
  font-weight: 300;
  text-decoration: none;
  border-bottom: solid 1px;
}
a .bold {
  font-weight: bold;
}
a .bold:hover {
  font-weight: bold;
}
a .boldhover {
  font-weight: 300;
}
a .boldhover:hover {
  font-weight: bold;
}

ul {
  padding-left: 26px;
  list-style-type: "»  ";
}

.btn-lg {
  font-family: "PT Serif", serif;
  text-transform: lowercase;
}

.navbar .navbar-brand {
  font-family: "PT Serif", serif;
  font-weight: 500;
  font-size: 50px;
  line-height: 14px;
}
.navbar .nav-link {
  font-family: Dosis, sans-serif;
}

.nav-tabs ul {
  margin: 16px 0 0 0;
  padding: 0 10px 0 10px;
  border-bottom: solid 1px #ddd;
}
.nav-tabs ul li {
  margin: 0 0 -1px 0;
  padding: 3px 12px;
  display: inline-block;
}
.nav-tabs ul li.active {
  -moz-border-radius-topleft: 8px;
  border-top-left-radius: 8px;
  -moz-border-radius-topright: 8px;
  border-top-right-radius: 8px;
  border-left: solid 1px #ddd;
  border-top: solid 1px #ddd;
  border-right: solid 1px #ddd;
  border-bottom: solid 1px #fff;
}
.nav-tabs ul a {
  font-weight: 300;
  text-transform: lowercase;
  color: #1d3154;
}

.jumbotron {
  padding: 2rem 2rem;
}
.jumbotron > h1 {
  margin-top: 0.25rem;
  margin-bottom: 0.5rem;
  font-weight: 700;
}

footer {
  margin: 50px 0 0 0;
}
footer p {
  font-size: 1rem;
  font-weight: 300;
}

#resume-link :hover {
  border-bottom: solid 0px;
}

.external-profiles li a img {
  height: 32px;
  margin: 0 4px 0 0;
}